import React, { Component } from "react";
import { connect } from "react-redux";

import Spinner from "../../components/UI/Spinner/Spinner";

import Order from "../../components/Order/Order";
import axios from "../../axios-orders";
import withErrorHandler from "../../hoc/withErrorHandler/withErrorHandler";

import * as actions from "../../store/actions/index";

class Orders extends Component {
  /* state = {
    orders: [],
    loading: true,
  } */

  componentDidMount() {
    this.props.onFetchOrders(this.props.token, this.props.userId);
    /* axios.get("/orders.json")
            .then(res => {
                const fetchedOrders = [];
                for(let key in res.data) {
                    fetchedOrders.push({
                        ...res.data[key],
                        id: key
                    });
                }

                this.setState({                    
                    loading: false,
                    orders: fetchedOrders
                });
            })
            .catch(err => {
                this.setState({
                    loading: false
                });
            })  */
  }

  render() {
    let orders = null;

    // if (this.state.loading) {
    if (this.props.loading) {
      orders = <Spinner />;
    } else {
      orders = <h1 style={{ textAlign: "center" }}>No orders</h1>;

      // if (this.state.orders.length !== 0) {
      if (this.props.orders.length !== 0) {
        // orders = this.state.orders.map(el => {
        orders = this.props.orders.map(el => {
          const price = el["price"];
          const ingredients = el["ingredients"];

          const ingrArr = [];
          for (let ingr in ingredients) {
            ingrArr.push([ingr, ingredients[ingr]]);
          }

          //const ingrArr = Object.entries(ingredients);

          return <Order key={el["id"]} ingredients={ingrArr} price={price} />;
        });
      }
    }

    return <div>{orders}</div>;
  }
}

const mapStateToProps = state => {
  return {
    orders: state.order.orders,
    loading: state.order.loading,
    token: state.auth.token,
    userId: state.auth.userId
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onFetchOrders: (token, userId) => dispatch(actions.fetchOrders(token, userId))
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withErrorHandler(Orders, axios));
